import {$, Component} from '@zui/core';
import {Modal} from '@zui/modal';
import {Messager} from '@zui/messager';

import type {Cash} from '@zui/core';
import type {ModalAlertOptions} from '@zui/modal';
import type {AjaxFormOptions, AjaxFormResult, AjaxFormEvents} from '../types';

const createSelector = (name: string) => {
    return name.replace(/([[\]])/g, '\\$1');
};

export class AjaxForm extends Component<AjaxFormOptions, AjaxFormEvents, HTMLFormElement> {
    static readonly NAME = 'AjaxForm';

    init() {
        this.$element.on('submit', this.onSubmit.bind(this)).on('input mousedown change', this.onInput.bind(this));
    }

    enable(toggle = true) {
        this.$element.toggleClass('loading', !toggle);
    }

    disable() {
        this.enable(false);
    }

    onInput(event: MouseEvent) {
        const $control = $(event.target as HTMLElement).closest('.has-error');
        if (!$control.length) {
            return;
        }
        let name = $control.attr('name') as string;
        if (!name) {
            name = $control.find('.pick-value').attr('name') as string;
        }
        $control.removeClass('has-error');
        $control.closest('.form-group,.form-batch-control').find(`#${name.split('[')[0]}Tip,#${createSelector(name)}Tip`).remove();
    }

    onSubmit(event: SubmitEvent) {
        event.preventDefault();

        const {element} = this;
        const options = $.extend({}, this.options);
        this.emit('before', event, element, options);
        const submit = () => {
            this.disable();
            this._send(this._processData()).finally(() => {
                this.enable();
            });
        };
        const result = options.beforeSubmit?.(event, element, options);
        if (result === false) {
            return;
        }
        if (result instanceof Promise) {
            result.then(confirmed => confirmed && submit());
            return;
        }
        submit();
    }

    submit() {
        this.element.submit();
    }

    reset() {
        this.element.reset();
    }

    _processData(): FormData {
        const {element, options} = this;
        let formData = new FormData(element);
        let {submitEmptySelectValue = ''} = options;
        if (submitEmptySelectValue !== false) {
            if (typeof submitEmptySelectValue !== 'boolean') {
                submitEmptySelectValue = '';
            }
            $(element).find('select').each((_, select) => {
                const $select = $(select);
                const name = $select.attr('name')!;
                if (!formData.has(name)) {
                    formData.append(name, (typeof submitEmptySelectValue === 'object' ? submitEmptySelectValue[name] : submitEmptySelectValue) as string);
                }
            });
        }

        const {beforeSend} = options;
        if (beforeSend) {
            const result = beforeSend(formData);
            if (result instanceof FormData) {
                formData = result;
            }
        }

        this.emit('send', formData);
        return formData;
    }

    _send(formData: FormData) {
        const {$element, options} = this;
        let error: Error | undefined;
        let result: AjaxFormResult | undefined;
        return $.ajax(options.url || $element.attr('action') as string, {
            method: $element.attr('method') as string || 'POST',
            data: formData,
            headers: $element.closest('.modal').length ? {'X-ZUI-Modal': 'true'} : undefined,
            dataType: 'json',
        }).success((data) => {
            result = data as AjaxFormResult;
            if (!result || typeof result !== 'object') {
                error = new Error('Invalid json format');
            }
        }).fail(err => {
            error = err;
            console.warn('ZUI: cannot send ajax form', err);
        }).complete(() => {
            if (error) {
                this.emit('error', error);
                options.onError?.(error);
            } else {
                this._handleResult(result!);
            }
            this.emit('complete', result, error);
            options.onComplete?.(result, error);
        });
    }

    _showValidateMessage(message: Record<string, string | string[]>) {
        let $firstControl: Cash | undefined;
        Object.entries(message).forEach(([name, msg]) => {
            if (Array.isArray(msg)) {
                msg = msg.join('');
            }
            const nameSelector = createSelector(name);
            const {$element} = this;
            let $control = $element.find(`#${nameSelector}`);
            if (!$control.length) {
                $control = $element.find(`[name="${nameSelector}"]`);
            }
            if (!$control.length && !name.includes('[')) {
                $control = $element.find(`[name="${nameSelector}[]"]`);
            }
            if (!$control.length) {
                Modal.alert({message: msg} as ModalAlertOptions);
                return;
            }
            if ($control.hasClass('pick-value')) {
                $control = $control.closest('.pick');
            }
            $control.addClass('has-error');
            const $group = $control.closest('.form-group,.form-batch-control');
            if ($group.length) {
                let $tip = $group.find(`#${nameSelector}Tip`);
                if (!$tip.length) {
                    $tip = $(`<div class="form-tip ajax-form-tip text-danger pre-line" id="${name}Tip"></div>`).appendTo($group);
                }
                $tip.empty().text(msg);
            }
            if (!$firstControl) {
                $firstControl = $control;
            }
        });
        if ($firstControl) {
            $firstControl[0]?.focus();
        }
    }

    _handleResult(result: AjaxFormResult) {
        const {options} = this;
        const {message} = result;
        if (result.result === 'success') {
            this.emit('success', result);
            if (options.onSuccess?.(result) === false) {
                return;
            }

            if (typeof message === 'string' && message.length) {
                Messager.show({content: message, type: 'success'});
            }
        } else {
            this.emit('fail', result);
            if (options.onFail?.(result) === false) {
                return;
            }

            // Handle message
            if (message) {
                if (typeof message === 'string' && message.length) {
                    Modal.alert({message: message} as ModalAlertOptions);
                } else if (typeof message === 'object') {
                    this._showValidateMessage(message);
                }
            }
        }

        const closeModal = options.closeModal ?? result.closeModal;
        if (closeModal) {
            Modal.hide(typeof closeModal === 'string' ? closeModal : undefined);
        }

        // Handle callback
        const callback = options.callback ?? result.callback;
        if (callback) {
            const callbackParams: [string, unknown][] = [['options', options], ['result', result]];
            if (typeof callback === 'string') {
                const func = $.runJS(callback, ...callbackParams);
                if (typeof func === 'function' && !callback.endsWith(';')) {
                    func(result);
                }
            } else if (typeof callback === 'object') {
                const func = $.runJS(callback.name, ...callbackParams);
                if (typeof func === 'function') {
                    func.apply(this, Array.isArray(callback.params) ? callback.params : [callback.params]);
                }
            }
        }

        // Handle locate
        const load = options.load ?? (result.load ?? result.locate);
        if (load) {
            this.$element.trigger('locate.zt', load);
        }
    }
}
