import {Component} from '@zui/core';

type GanttOptions = {
    initGantt: () => void;
    destroyGantt: () => void;
};

export class Gantt extends Component<GanttOptions, {}, HTMLElement> {
    static NAME = 'Gantt';

    init() {
        const {initGantt} = this.options;
        if (typeof initGantt === 'function') {
            initGantt();
        }
    }

    destroy() {
        const {destroyGantt} = this.options;
        if (typeof destroyGantt === 'function') {
            destroyGantt();
        }
        super.destroy();
    }
}
